@import 'src/variables.scss';

.footer {
  @include flex(space-between);
  flex-direction: column;
  z-index: 1;
  width: 100%;
  background: linear-gradient(to right, #191919, #000000);
  padding: 40px 0 0;
  min-height: 280px;


  .footer-layout {
    @include screenLayout();
    @include flex(space-between);
    flex-wrap: nowrap;
    align-items: flex-start;

    .website {
      width: 28%;

      .contact-us-fooetr {
        @include flex(flex-start);
        margin: 5px auto 0;

        a {
          display: inline-block;
          margin-right: 24px;
        }
      }

      p {
        color: #8A9199;
        margin-bottom: 22px;

        img {
          height: 35px
        }
      }
    }

    .footer-nav {
      @include flex(flex-start);
      align-items: flex-start;
      flex-wrap: nowrap;
      min-width: 440px;

      li {
        &:not(:first-child) {
          margin: 0 0 0 115px;
        }

        p {
          font-size: 18px;
          color: #FFFFFF;
          margin: 0 0 30px;
          font-weight: bold;
        }

        div {
          margin: 0 0 15px;

          a {
            color: #8A8D99;
            font-weight: bold;

            &:last-child {
              margin: 0;
            }

            // &:hover {
            //   transition: color 0.3s;
            //   color: $bg-color;
            // }
          }

          span {
            display: block;
            color: #8A8D99;
            line-height: 1;
            font-size: 12px;
            font-weight: 400;
          }
        }
      }
    }
  }

  .copyright {
    @include screenLayout();
    @include heightLine(50px);
    text-align: center;
    color: #7A8199;
    margin: 40px 0 0;
    border-top: 1px solid #2D2D2D;
  }
}