@import 'src/variables';

.service-wrap {
  .ant-drawer-content-wrapper {

    .ant-drawer-content {
      .ant-drawer-wrapper-body {
        .ant-drawer-header {
          background: linear-gradient(to right, #070707, #3D3D3D);

          .ant-drawer-header-title {
            .ant-drawer-close {
              position: absolute;
              right: 24px;
              height: 30px;
              width: 30px;
              border-radius: 50%;
              background: rgba(#FFFFFF, 0.16);

              svg {
                color: #FFF;
              }
            }

            .ant-drawer-title {
              color: #FFF;
              font-size: 20px;
            }
          }

        }

        .ant-drawer-body {
          background-color: #F7F9FA;
          padding: 30px;

          .container {
            border-radius: 12px;
            background: #FFF;
            padding: 14px 24px;

            .main-feature {
              width: 100%;
              @include flex(space-between);
              border-bottom: 1px solid #F2F5F7;
              padding-bottom: 12px;

              a,
              >span {
                color: #8A9199;
                cursor: pointer;

                &.active {
                  color: #000;
                }
              }
            }

            .article {

              a {
                display: block;
                color: #000;
                margin-bottom: 12px;

                &:first-child {
                  margin-top: 17px;
                }
              }
            }

            &.tips {
              color: #8A9199;
              margin: 16px 0;
              font-size: 12px;
            }

            &.WhatAPP {
              p {
                &:first-child {
                  padding-bottom: 10px;
                  border-bottom: 1px solid #F2F5F7;
                  font-weight: bold;
                }

                &:nth-child(2) {
                  margin: 10px 0;
                  height: 70px;

                  >a,
                  >a:hover {
                    color: #1568DF;
                  }
                }
              }
            }

          }
        }
      }
    }
  }

}