@import 'src/variables';

.header-wrap {
  width: 100%;
  height: 64px;
  background: #000000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 40px;
  font-size: 0.4rem;

  a {
    height: 64px;
  }
}

.header-wrap>div:first-child {
  display: flex;
  align-items: center;

  .logo {
    width: 81px;
    margin-right: 16px;
    cursor: pointer;
  }

  span {
    color: white;
    font-size: 18px;
  }
}

.header-wrap>div:last-child {
  display: flex;
  align-items: center;

  .account {
    height: inherit;
    font-size: 14px;
    color: #FFF;
    padding: 6px 15px;
    border-radius: 8px;
    border: 1px solid #FFF;
    margin-right: 26px;

    .icon {
      margin-right: 10px;
    }
  }

  .connect-service {
    display: flex;
    align-items: center;
    color: $primary-color;
    margin-right: 26px;
    cursor: pointer;

    span {
      font-size: 16px;
    }

    .header-icon {
      width: 24px;
      margin-right: 8px;
    }
  }
}