@import 'src/variables';

.lang-select-wrap {
  position: relative;

  div:first-child {
    color: #FFFFFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;

    span {
      font-size: 16px;
    }
  }

  .select-content {
    position: absolute;
    right: 0;
    top: 60px;
    width: 150px;
    background-color: #FFFFFF;
    border-radius: 8px;
    color: #979797;
    line-height: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 15px 0;
    font-size: 16px;
    z-index: 100;

    p {
      cursor: pointer;
    }

    .active-lan {
      color: #000000;
    }
  }
}

@media screen and (max-device-width: 640px) {
  .lang-select-wrap {
    position: relative;

    div:first-child {
      color: #FFFFFF;
      display: flex;
      align-items: center;
      // width: 0.45rem;
      // height: 0.24rem;
      justify-content: space-between;
      cursor: pointer;
      // padding-right: 0.14rem;

      .icon-lang {
        height: 0.44rem;
      }

      span {
        font-size: 0.16rem;
      }
    }

    .select-content {
      position: absolute;
      right: 0;
      top: 0.6rem;
      width: 2rem;
      height: 1.76rem;
      background-color: #FFFFFF;
      border-radius: 0.16rem;
      color: #8A9199;
      line-height: 0.4rem;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      padding: 0.32rem 0;
      font-size: 0.28rem;
      z-index: 100;
      box-shadow: 0 6px 12px rgba(#000, 0.1);

      p {
        cursor: pointer;
      }

      .active-lan {
        color: #000;
      }
    }
  }
}