.empty-wrap {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 16px;
  line-height: 22px;
  color: #5C6673;

  .icon {
    width: 240px;
    margin-bottom: 11px;
  }
}

@media screen and (max-device-width: 640px) {
  .empty-wrap {
    font-size: 0.24rem;
    line-height: 0.33rem;

    .icon {
      height: 1.4rem;
      margin-bottom: 0.32rem;
    }
  }
}