@import 'src/variables.scss';

@media (max-device-width: 640px) {
  .footer-mobile {
    width: 100%;
    padding: 0 0.32rem;
    background: #000000;


    .footer-nav {
      width: 100%;
      padding-top: 0.32rem;
      // padding: 0.26rem 0.32rem;
      margin: 0;

      li {
        color: $bg-color;
        font-size: 0.16rem;
        font-weight: 600;
        width: 100%;
        overflow: hidden;
        transition: height 0.3s ease-in;

        .summary {
          height: 44px;
          width: 100%;
          @include flex(space-between);
          color: #FFF;
          font-size: 0.32rem;
          font-weight: bold;

          img {
            height: 0.09rem;
          }

        }

        .contant {
          >div {

            a {
              height: 0.6rem;
              color: #8A9199;
              font-size: 0.28rem;
              line-height: 0.6rem;
            }

            span {
              color: #8A9199;
              font-size: 0.1rem;
            }

            // &:last-child {
            //   padding: 0 0 0.4rem;
            // }

          }
        }
      }
    }

    .contact-us-text {
      height: 0.44rem;
      line-height: 0.44rem;
      color: #FFF;
      font-size: 0.32rem;
      font-weight: bolder;
      margin: 0.2rem 0 0.2rem;
    }

    .contact-us-fooetr {
      margin: 0.2rem 0.2rem 0.2rem 0;

      a {
        margin-right: 0.2rem;

        img {
          height: 0.23rem;
        }

        &:last-child {
          margin-right: 0;
          height: 0.18rem;

          img {
            height: 0.18rem;
          }
        }
      }

    }

    .copyright {
      margin: 0;
      padding: 0.18rem 0 0.48rem;
      text-align: center;
      color: #8A9199;
      font-size: 0.12rem;
      border-top: 1px solid #2D2D2D;
    }
  }
}