@import 'src/variables.scss';

.search-content {
  width: 100%;
  height: 162px;
  background: #050505;

  .bg-container {
    @include screenLayout();
    @include flex();
    height: 162px;
    overflow: hidden;

    p {
      font-size: 40px;
      color: #FFFFFF;
      margin: 0 96px;
      white-space: nowrap;
    }

    >img {
      height: 94px;
    }
  }

  .input-wrap {
    width: 613px;
    height: 60px;
    margin: -30px auto 0;
    @include flex();
    position: relative;
    box-shadow: 0 6px 10px rgba($theme-color, 0.1);
    border-radius: 12px;
    border: 1px solid #FFFFFF;
    background-color: #FFFFFF;
    padding: 20px 12px 20px 24px;

    .search-icon {
      margin-left: -15px;
      position: absolute;
      width: 24px;
      left: 35px;
      top: 50%;
      transform: translateY(-50%);
    }

    input {
      height: 58px;
      width: 533px;
      margin-left: 40px;
      font-size: 16px;
      color: $theme-color;
      outline: none;
      border: none;
      // &::placeholder{
      //   color: #C0C6CC;
      // }
      &::-webkit-input-placeholder {
        color: #C0C6CC;
      }
    }

    .search-button {
      background-color: $theme-color;
      border-radius: 8px;
      height: 36px;
      width: 80px;
      text-align: center;
      line-height: 36px;
      color: #FFFFFF;
      cursor: pointer;
    }
  }
}

@media screen and (max-device-width: 640px) {
  .search-content {
    height: inherit;
    margin: 0.4rem 4% 0;
    width: inherit;
    position: relative;
    z-index: 8;
    // border-radius: 0.24rem;
    box-shadow: 0 2px 7px rgba(#000000, 0.08);
    background: transparent;
    border-radius: 0.26rem;
    border-width: 0px;

    .mobile-content {
      padding: 0.4rem 0 0.32rem;
      @include flex(space-between);
      flex-direction: column;
      width: 100%;
     background: #FFF;
      border-radius: 0.24rem;

      .bg-container {
        display: block;
        margin-bottom: 0.32rem;
        // justify-content: flex-start;
        // align-items: flex-start;
        padding: 0 0.32rem;
        height: inherit;

        p {
          margin: 0;
          font-size: 0.48rem;
          color: black;
          font-weight: 600;
        }
      }

      .input-wrap {
        position: relative;
        width: calc(100% - 0.52rem);
        padding: 0;
        border: none;
        // border: 1px solid #97979770;
        background-color: #F7F9FA;
        border-radius: 0.2rem;
        margin: 0 0.32rem;
        height: 0.84rem;
        box-shadow: none;

        .search-icon {
          margin-left: 0;
          width: 0.28rem;
          position: absolute;
          left: 0.32rem;
        }

        input {
          height: 0.84rem;
          width: inherit;
          background: transparent;
          font-size: 0.28rem;
          color: $theme-color;
          outline: none;
          box-sizing: border-box;

          &::placeholder {
            color: #8594A6;
            font-size: 0.28rem;
          }
        }
      }
    }
  }
}