  .service-title {
    font-size: 0.32rem;
    display:flex;
    justify-content:space-between;
    height: 0.88rem;
    line-height: 0.88rem;
    padding: 0 0.32rem;
    background: #FFF;
    box-shadow: 0 6px 10px rgba(#FFF, 0.1);
  }

  .serve-box {
    background-color: #F7F9FA;
    padding: 0.32rem 0.32rem 1rem;

    .feature-box {
      border-radius: 0.24rem;
      padding: 0.28rem 0.32rem 0.04rem;
      background-color: #FFF;

      .main-feature {
        width: 100%;
        @include flex(space-between);
        flex-wrap: nowrap;
        box-sizing: border-box;
        border-bottom: 1px solid #ECECEC;
        background-color: #FFF;
        padding-bottom: 0.24rem;
        white-space: nowrap;
        overflow-x: hidden;
        // background-color: #FFFFFF;
        // padding: 0.26rem 0.32rem;

      a{
        margin-right: 0.24rem;
        color: #8A9199;
        span {
          display: inline-block;
          font-size: 0.28rem;
          color: #8A9199;
        }
      }
      }

      .article {
        background-color: #FFF;

        a {
          display: block;
          color: #000;
          margin-bottom: 0.24rem;
          &:first-child{
            margin-top: 0.34rem;
            // background-colorgb(3, 3, 3)red;
          }
        }
      }
    }

    .container {
      border-radius: 0.24rem;
      padding: 0.3rem 0.32rem;
      background-color: #FFF;
      margin: 0.24rem 0;
      color: #8A9199;
      font-size: 0.24rem;

      &.WhatAPP {
        color: #000;

        p {
          &:first-child {
            padding-bottom: 0.22rem;
            border-bottom: 1px solid #F2F5F7;
            font-weight: bold;
          }

          &:nth-child(2) {
            margin: 0.2rem 0;
            height: 1.6rem;
            >a, >a:hover{
              color: #1568DF;
            }
          }
        }

        // ul {
        //   li {
        //     line-height: 1.6;
        //   }
        // }
      }
    }

  }



  .message-wrap {
    width: 100%;
    margin-top: 0.32rem;
    height: calc(100vh - 3.28rem);
    overflow: auto;
    padding-bottom: 0.4rem;
  }

  .bottom-wrap {
    width: 100%;
    height: 2rem;
    background-color: #FFFFFF;
    padding: 0.19rem 0.12rem 0.19rem 0.32rem;

    .features-wrap {
      width: 100%;
      height: 0.4rem;
      margin-bottom: 0.26rem;
      display: flex;
      flex-wrap: nowrap;

      span {
        font-size: 0.24rem;
        line-height: 0.4rem;
        display: inline-block;
        text-align: center;
        height: 0.4rem;
        max-width: 1.7rem;
        background-color: #F7F9FA;
        border-radius: 0.2rem;
        padding: 0 0.13rem;
        margin-right: 0.2rem;
        // color: #F2F2F2;
        color: #000000;
        overflow: hidden;
        font-weight: 300;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }

    .send-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 6.86rem;

      span:first-child {
        display: inline-block;
        width: 4.72rem;
        height: 0.88rem;
        background-color: #F7F9FA;
        border-radius: 0.08rem;
      }

      span:last-child {
        display: inline-block;
        width: 1.98rem;
        height: 0.88rem;
        color: #FFFFFF;
        background-color: #000000;
        border-radius: 0.08rem;
        text-align: center;
        line-height: 0.88rem;
        font-size: 0.36rem;
      }
    }
  }
}