@import 'src/variables.scss';

.pagination-wrap {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  line-height: 20px;
  color: #8594A6;

  span {
    display: inline-block;
    text-align: center;
    height: 32px;
    min-width: 32px;
    line-height: 32px;
    text-align: center;
    background: #FFF;
    margin-right: 8px;
    cursor: pointer;
    border-radius: 6px;
    color: #000;
    border: 1px solid #F2F5F7;

    &:hover {
      border-color: #000;
      // color: #FFF;
    }
  }

  .actived-item {
    background-color: #000;
    color: #FFF;
  }

  .disabled-item {
    cursor: not-allowed;
    background-color: #F7F9FA;
    color: #8891A2;
    border: none;
  }
}

.pagination-wrap span:last-child {
  margin-right: 0;
  background-color: #F7F9FA;
  color: #8891A2;
}


@media screen and (max-device-width: 640px) {
  .pagination-wrap {
    font-size: 0.28rem;
    line-height: 0.4rem;

    span {
      padding: 0.06rem 0.1rem;
      margin-right: 0.16rem;
      border-radius: 0.04rem;
      min-width: 0.48rem;
    }
  }
}