@import 'src/variables.scss';

.categories-wrap {
  width: 100%;
  // border-radius: 70px 70px 0 0; 
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 76px 0;

  .categories-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 1240px;

    >div {

      &:hover,
      &.active {
        background-color: #000000;
        color: #FFFFFF;
      }

      box-sizing: border-box;
      width: 384px;
      @include flex();
      height: 60px;
      background-color: #F7F9FA;
      border-radius: 12px;
      font-size: 16px;
      font-weight: 500;
      cursor: pointer;
      margin-bottom: 24px;
      color: #000000;
    }
  }

  .question-contain {
    @include screenLayout();
    @include flex(flex-start);
    align-items: flex-start;
    padding: 26px 0 0;

    .directory {
      width: 224px;
      padding: 0;

      li {
        cursor: pointer;
        text-align: left;
        height: 44px;

        &.active {
          list-style: inherit !important;
        }
      }
    }

    .details {
      padding: 0 0 0 44px;
      width: calc(100% - 224px);
      border-left: 1px solid #F2F5F7;

      .title {
        font-size: 24px;
        color: $theme-color;
        font-weight: 500;
      }

      .text {
        width: 100%;
        padding: 0;
        // margin-top: 20px;
        margin: 20px 0 31px;

        a {
          color: #000;
          width: 100%;
          display: inline-block;

          li {
            @include flex(space-between);
            height: 56px;
            border-top: 1px solid #F2F5F7;
            margin: 0 16px;

            >p {
              font-size: 16px;
            }

            >span {
              font-size: 12px;
              color: rgba(#8A9199, .9);
            }

            &:first-child {
              border-top: none;
            }
          }

          &:hover {
            background-color: #F2F5F7;
            border-radius: 12px;
            border-top: none;
          }
        }
      }

      .ant-pagination {
        text-align: right;
      }
    }
  }
}

@media screen and (max-device-width: 640px) {
  .categories-wrap {
    width: 100%;
    margin-top: -1.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2.05rem 0 0;

    .categories-content {
      width: 100%;
      padding: 0 0.32rem;
      // min-height: 4.74rem;
      @include flex(space-between);
      flex-wrap: nowrap;
      overflow-x: scroll;
      // scrollbar-width: none;
      /* firefox */
      // -ms-overflow-style: none;
      /* IE 10+ */
      border-bottom: 1px solid #F2F5F7;

      >div {
        width: inherit;
        height: inherit;
        position: relative;
        color: #8A9199;
        background: #FFFFFF;
        margin-bottom: 0;
        font-size: 0.28rem;
        margin-right: 0.24rem;
        white-space: nowrap;
        padding-bottom: 0.2rem;
        transition: all 0.3s;
        user-select: none;

        &:hover,
        &.active {
          color: #000;
          background: #FFFFFF !important;
          font-weight: 700;

          &::after {
            content: '';
            display: inline-block;
            width: 1rem;
            height: 0.03rem;
            background: #000;
            position: absolute;
            left: 0;
            top: 0.6rem;
            z-index: 10;
            // transform-origin: 0% 50%;
            // transform: scaleX(0) translateX(-50%);
            transition: all 0.5s;
            border-radius: 0.04rem;
          }
        }

      }
    }

    .categories-content::-webkit-scrollbar {
      display: none;
      /* Chrome Safari */
    }

    .question-contain {
      padding: 0 0.32rem 0.5rem;
      display: block;
      min-height: 50vh;

      .directory {
        width: 100%;
        overflow-x: scroll;
        scrollbar-width: none;
        /* firefox */
        -ms-overflow-style: none;
        /* IE 10+ */
        @include flex(flex-start);
        margin-top: 0.32rem;

        li {
          white-space: nowrap;
          height: inherit;
          background: #F7F9FA;
          border-radius: 0.12rem;
          padding: 0.12rem 0.24rem;
          font-size: 0.24rem;
          color: #8A9199;
          margin-right: 0.2rem;

          &.active {
            list-style: none !important;
            background: #000;
            color: #FFFFFF;
          }
        }
      }

      .directory::-webkit-scrollbar {
        display: none;
        /* Chrome Safari */
      }

      .details {
        width: 100%;
        padding: 0;
        border: none;

        .text {
          margin: 0.32rem 0;

          a {

            li {
              display: block;
              padding: 0.3rem 0;
              height: inherit;
              margin: 0;

              >p {
                font-size: 0.28rem;
                font-weight: bold;
              }

              >span {
                font-size: 0.2rem;
              }

              &:first-child {
                border-top: none;
              }
            }

            &:hover {
              background-color: #F2F5F7;
              border-radius: 12px;
              border-top: none;
            }
          }
        }
      }
    }
  }
}