@import 'src/variables.scss';

.service-page-wrap {
  width: 100%;

  .container-wrap {
    width: 100%;
    display: flex;
    justify-content: center;
    background-color: #FFFFFF;

    .content {
      width: 1240px;
      padding: 30px 0 100px 0;
      // background-color: #FFFFFF;

      .tabs {
        .interval-icon {
          color: #B4B4B4;
          font-size: 12px;
          line-height: 12px;
          margin: 0 8px;
        }

        span {
          color: #000000;
          font-size: 12px;
          line-height: 12px;
        }
      }

      .chat-wrap {
        width: 100%;
        display: flex;
        justify-content: center;
        padding-top: 100px;

        .chat {
          width: 800px;
          border-radius: 16px;
          border: 1px solid #ECECEC;

          .title {
            width: 100%;
            height: 62px;
            background-color: #000000;
            // background-color: $primary-color;
            border-radius: 15px 15px 0 0;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            font-size: 18px;
            color: #FFFFFF;
            padding: 0 27px;
            box-sizing: border-box;

            >span:last-child {
              font-size: 14px;
              line-height: 18px;
              text-align: center;
              border: 1px solid #FFFFFF;
              padding: 6px 10px;
              box-sizing: border-box;
            }
          }

          .content-wrap {
            display: flex;

            .left {
              width: 540px;
              border-right: 1px solid #ECECEC;

              .receive-message {
                padding: 15px 0 0 0;
                box-sizing: border-box;
                background-color: #F7F9FA;

                .main-feature {
                  width: 100%;
                  box-sizing: border-box;
                  border-top: 1px solid #ECECEC;
                  border-bottom: 1px solid #ECECEC;
                  background-color: #FFFFFF;
                  padding: 0 0 10px 15px;

                  span {
                    display: inline-block;
                    height: 28px;
                    border-radius: 14px;
                    background-color: #F7F9FA;
                    font-size: 12px;
                    line-height: 28px;
                    color: #000000;
                    padding: 0 14px;
                    margin: 10px 15px 0 0;
                  }
                }
              }

              .send-message {
                height: 153px;
                padding: 20px;
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;

                span {
                  display: block;
                  border-radius: 4px;
                  background-color: $primary-color;
                  font-size: 16px;
                  line-height: 32px;
                  height: 32px;
                  width: 72px;
                  text-align: center;
                }
              }
            }

            .right {
              width: 260px;
              padding: 31px 16px;
              font-size: 16px;
              color: #8594A6;
              line-height: 36px;

              .article-title {
                font-size: 20px;
                line-height: 20px;
                color: #000000;
                margin-bottom: 20px;
              }

              .article-name {
                font-size: 16px;
                color: #8594A6;
              }
            }
          }
        }
      }
    }
  }
}