$primary-color: #FFFFFF;
$bg-color: #1D2229;
$theme-color:#000000;

@mixin normalized-bg-styles {
  background-position: 0 0;
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

@mixin flex($justify: center) {
  display: flex;
  align-items: center;
  justify-content: $justify;
  flex-wrap: nowrap;
}

@mixin screenLayout {
  width: 1240px;
  padding: 0 20px;
  margin: 0 auto;

  @media screen {
    @media (max-device-width: 640px) {
      width: 100%;
      padding: 0;
    }
  }

}

@mixin heightLine($height) {
  height: $height;
  line-height: $height;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

