@import 'src/variables.scss';

.mobile-article-page {
  ::-webkit-scrollbar {
    display: none !important;
    /* Chrome Safari */
  }

  .article-content-wrap {

    ul {
      color: #8594A6;
    }

    li {
      color: #8594A6;
    }

    pre {
      white-space: normal;
    }

    table {
      min-width: 100%;
      overflow: scroll;
    }

    table tr th,
    table tr td {
      overflow: auto;
    }

    table,
    table tr th,
    table tr td {
      border: 0.01rem solid grey;
      border-collapse: collapse
    }

    .update-time {
      font-size: 0.2rem;
      line-height: 0.2rem;
      display: inline-block;
      color: #5C6673;
      margin: 0.24rem 0;
    }

    .tabs-wrap {
      padding: 0.24rem 0.32rem;
      box-shadow: 0 6px 10px rgba($theme-color, 0.1);
      font-size: 0.28rem;

      >a {
        span {
          color: #8A9199;
        }

        &:last-child {
          span {
            color: #000;
          }

        }
      }

      .interval-icon {
        color: #B4B4B4 !important;
        margin: 0 8px;
      }
    }

    .title {
      margin-top: 0.48rem;
      padding: 0 0.32rem;
      font-size: 0.36rem;
      text-align: left;
      font-weight: 600
    }

    // .update-time>span:first-child {
    //   margin-right: 0.52rem;
    // }
    .update-time {
      @include flex(flex-start);
      font-size: 0.32rem;
      color: #5C6673;
      padding: 0 0.32rem 0.4rem;
      border-bottom: 1px solid #F2F5F7;

      >svg {
        height: 0.72rem;
      }

      >div {
        margin-left: 0.26rem;

        span {
          line-height: 0.4rem;
          display: block;
          font-size: 0.2rem;
          // line-height: 0.3rem;

          &:first-child {
            color: #000;
            font-size: 0.32rem;
          }
        }
      }
    }

    .article-content {
      color: #000000;
      font-size: 0.28rem;
      line-height: 0.52rem;
      margin-bottom: 0.5rem;
      padding: 0 0.32rem;
      overflow-x: scroll;
      scrollbar-width: none;
      /* firefox */
      -ms-overflow-style: none;
      /* IE 10+ */
      -ms-overflow-style: none;
      scrollbar-width: none;
      p{
        >a {
          color: #1568DF;
        }
      }
    }

    .article-content::-webkit-scrollbar {
      display: none;
      /* Chrome Safari */
    }

    .link-box {
      padding: 0 0.32rem;
      font-size: 0.28rem;

      .downloads-link {
//         display: inline-block;
        color: #000;
//         margin-bottom: 0.1rem;
//         margin-left: -0.3rem;
      }

      >p {
        margin-bottom: 0.1rem;
        font-size: 0.28rem;

        span {
          display: block;
        }

        a {
          display: block;
          color: #1568DF;
        }
      }
    }

    .back {
      background-color: #000;
      padding-bottom: 0.4rem;

      .icon {
        @include flex(center);
        justify-content: flex-start;
        height: 0.88rem;
        line-height: 0.88rem;
        padding: 0.1rem 0;
        padding: 0 0.32rem;
        border-bottom: 1px solid #2D2D2D;
        font-size: 0.28rem;
        color: #FFF;

        svg {
          height: 0.3rem;
          margin-right: 0.3rem;
        }
      }

      .statement {
        height: 1.2rem;
        @include flex(center);
        color: #8A9199;
        font-size: 0.2rem;
      }
    }

  }

  .article-content-wrap>p {
    font-size: 0.4rem;
    color: #000000;
  }

}