@import 'src/variables.scss';

.pc-article-page {
  width: 100%;

  .content-wrap {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    background-color: #FFFFFF;

    .article-content-wrap {
      width: 1240px;
      padding: 0 0 100px 0;
      min-height: calc(100vh - 484px);
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      .left-content {
        width: 820px;

        .tabs-wrap {
          display: inline-block;
          border: 1px solid #E1E3E6;
          font-size: 12px;
          line-height: 12px;
          margin: 74px 0 50px 0;
          padding: 13px 16px;
          border-radius: 8px;

          >div {
            display: inline-block;

            span {
              color: #8A9199;
            }

            &:last-child {
              span {
                color: #000;
              }

            }
          }

          .interval-icon {
            color: #B4B4B4 !important;
            font-size: 12px;
            line-height: 12px;
            margin: 0 8px;
          }
        }

        .title {
          font-size: 32px;
        }

        .update-time {
          @include flex(flex-start);
          font-size: 14px;
          line-height: 14px;
          color: #5C6673;
          padding: 32px 0 30px;
          border-bottom: 1px solid #F2F5F7;
          margin: 0 0 20px;

          >div {
            margin-left: 10px;

            p {
              line-height: 20px;

              &:first-child {
                color: #000;
                font-size: 18px;
              }
            }
          }
        }

        // .update-time>span:first-child {
        //   display: inline-block;
        //   height: 14px;
        //   margin-right: 20px;
        //   border-right: 1px solid #2C313D;
        //   padding-right: 20px;
        // }

        .article-content {
          margin-bottom: 40px;
          font-size: 16px;
          line-height: 40px;
          color: #000000;

          ul {
            // color: #F2F2F2;
            color: #000000;
          }

          li {
            // color: #F2F2F2;
            color: #000000;
          }

          a {
            // color: $primary-color;
            color: #000000;
          }

          pre {
            white-space: normal;

          }

          table,
          table tr th,
          table tr td {
            border: 1px solid grey;
            border-collapse: collapse
          }
        }

        .link-url {
          line-height: 35px;
          font-weight: 500;

          a {
            color: #0D68EB;
          }
        }
      }

      .right-content {
        width: 256px;
        padding-top: 275px;
        position: relative;
        height: 100%;
        overflow-y: scroll;

        .search-icon {
          width: 24px;
          position: absolute;
          top: 112px;
          left: 40px;
        }

        input {
          background-color: #F7F9FA;
          height: 64px;
          width: 161px;
          border: none;
          border-radius: 16px;
          padding: 0 0 0 95px;
          font-size: 20px;
          color: #5C6673;
          outline: none;
        }

        .result-title {
          font-weight: bold;
          font-size: 18px;
          line-height: 18px;
          margin: 45px 0 20px 0;
        }

        .article-title {
          color: #8594A6;
          font-size: 14px;
          line-height: 28px;
          cursor: pointer;
        }

        .actived-article-title {
          color: #000000;
        }
      }

    }
  }
}