@import 'src/variables.scss';

.search-page-wrap {
  width: 100%;

  .search-content-wrap {
    // width: 100%;

    .content-wrap {
      width: 100%;
      display: flex;
      justify-content: center;
      border-radius: 70px 70px 0 0;
      background-color: #FFFFFF;

      .content {
        width: 1240px;
        padding: 75px 0 26px;
        min-height: 50vh;

        .tabs {
          display: inline-block;
          font-size: 12px;
          line-height: 12px;
          border: 1px solid #E1E3E6;
          border-radius: 8px;
          padding: 13px;

          a {
            span {
              color: #8A9199;
            }
          }

          span {
            color: #000000;
          }

          // &:last-child{

          // }

          .interval-icon {
            color: #B4B4B4;
            font-size: 12px;
            line-height: 12px;
            margin: 0 8px;
          }
        }

        .results {
          font-size: 14px;
          line-height: 14px;
          color: #8594A6;
          margin: 30px 0 20px 0;
          display: flex;
          justify-content: space-between;
        }

        .result-type {
          .types {
            font-size: 32px;
            line-height: 32px;
            color: #000000;
            margin-bottom: 32px;

            span {
              margin-right: 60px;
              cursor: pointer;
            }

            // .actived-type {
            //   position: relative;

            //   &:before {
            //     content: '';
            //     width: 40px;
            //     height: 8px;
            //     background-color: $primary-color;
            //     border-radius: 4px;
            //     position: absolute;
            //     top: 42px;
            //     left: 50%;
            //     transform: translateX(-50%);
            //   }
            // }

          }

          .result-article {
            font-size: 16px;
            color: #000;
            padding: 20px 16px;
            border-bottom: 1px solid #F2F5F7;
            @include flex(flex-start);
            flex-wrap: nowrap;
            width: 100%;

            &:hover {
              background-color: #F7F9FA;
              border-radius: 12px;
            }

            p:last-child {
              line-height: 28px;
            }
          }
        }

        .search-page {
          margin: 35px 0 100px;
          text-align: center
        }
      }
    }
  }
}


@media screen and (max-device-width: 640px) {
  .search-page-wrap {
    .search-content-wrap {
      margin-top: -1rem;

      .content-wrap {
        border-radius: 0.44rem 0.44rem 0 0;
        // margin-top: -0.42rem;

        .content {
          padding: 0.48rem 0;

          .results {
            font-size: 0.24rem;
            line-height: 0.33rem;
            margin: 0 5% 0.4rem 5%;

            span {
              font-size: 0.28rem;

              svg {
                margin-right: 0.1rem;
              }
            }
          }

          .load-more {
            color: $primary-color;
            font-size: 0.3rem;
            line-height: 0.3rem;
            margin: 0.55rem 0;
            width: 100%;
            text-align: center;
          }

          .result-type {
            padding: 0 0.32rem;

            .types {
              font-size: 0.4rem;
              line-height: 0.4rem;
              margin-bottom: 0.25rem;

              span {
                margin-right: 0.6rem;
              }

            }

            .result-article {
              font-size: 0.28rem;
              padding: 0.32rem 0;

              p:first-child {

                span:first-child {
                  font-size: 0.28rem;
                  font-weight: bold;
                }

                .updated {
                  display: block;
                  font-size: 0.2rem;
                  line-height: 0.2rem;
                  color: #8A9199;
                }
              }

              p:last-child {
                line-height: 28px;
              }
            }
          }

          .search-page {
            margin: 0.4rem 0 0.3rem;
          }

          .empty-wrapper {
            width: 100%;
            margin-bottom: 2rem;
          }

          .paginations {
            margin-top: 0.82rem;
          }
        }
      }
    }
  }
}