.chat-message-wrap {
  display: flex;
  flex-direction: row;
  font-size: 12px;
  line-height: 17px;
  color: #171B21;
  margin: 0 0 14px 19px;
  word-break: break-all;
  p {
    word-break: break-word
  }
  // .avatar {
  //   width: 32px;
  //   margin-right: 16px;
  // }

  .message-content {
    width: 360px;
    background-color: #FFFFFF;
    padding: 16px;
    border-radius: 0 8px 8px 8px;
    margin-top: 16px;
  }
}

@media screen and (max-device-width: 640px) {
  .chat-message-wrap {
    font-size: 0.32rem;
    line-height: 0.48rem;

    margin: 0 0.32rem;
    // margin: 0 0 0.42rem 0.32rem;
    // .avatar {
    //   width: 0.8rem;
    //   margin-right: 0.24rem;
    // }
  
    .message-content {
      width: 100%;
      // width: 5.32rem;
      padding: 0 0.32rem;
      padding: 0.26rem;
      border-radius: 0 0.16rem 0.16rem 0.16rem;
      margin-top: 0;
    }
  }
}