@import 'src/variables.scss';
.page-nav-bar-box{
  background: linear-gradient(to right, #1A1A1A, #000000);
  // background-color: #1D2229;
  &.search{
    height: 2.2rem;
  }
  .page-nav-bar {
    width: 100%;
    // height: 0.88rem;
    padding: 0.4rem 0.32rem 0.3rem;
    display: flex;
    align-items: center;
    color: #FFFFFF;
    background: linear-gradient(to right, #1A1A1A, #000000);
    .nav-logo{
      height: 0.6rem;
    }
  
    // .nav-title {
    //   color: #FFFFFF;
    //   font-size: 0.24rem;
    //   line-height: 0.24rem;
    // }
  
    // .interval-icon {
    //   color: #B4B4B4;
    //   font-size: 0.24rem;
    //   line-height: 0.24rem;
    //   margin: 0 0.16rem;
    // }
  
    .nav-bar-extra {
      flex: 1;
  
      .connect-service {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        font-size: 0.24rem;
        line-height: 0.24rem;
  
        .bar-icon {
          height: 0.44rem;
          margin: 0 0.08rem 0 0.16rem;
          position: relative;
  
          &:before {
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 0.01rem;
            background-color: #8594A6;
            height: 0.22rem;
          }
        }
  
        span {
          color: $primary-color;
        }
  
        >a {
          margin-right: 0.5rem;
          // border-right: 0.01rem solid #979797;
        }
      }
    }
  }
}